import React from "react";
import { Bar } from "react-chartjs-2";

const BarChart = ({ label, data }) => {
  return (
    <>
      <Bar
        data={{
          labels: label,
          datasets: [
            {
              label: "Label-1",
              backgroundColor: "rgb(34 73 241)",
             
              borderColor: "transparent",
              borderDash: [],
              borderDashOffset: 0.0,
              data: data,
              
            },
           
            
          ],
        }}
        options={{
          maintainAspectRatio: false,
          legend: {
            display: false,
          },
          tooltips: {
            backgroundColor: "#f5f5f5",
            titleFontColor: "#333",
            bodyFontColor: "#666",
            bodySpacing: 4,
            xPadding: 12,
            mode: "nearest",
            intersect: 0,
            position: "nearest",
          },
          responsive: true,
          scales: {
            yAxes: [
              {  
                gridLines: {
                  display: true,
                },
                stacked:true,
              },
            ],
            xAxes: [ 
              { 
                gridLines: {
                  display: true,
                },
                stacked:true,
              },
            ],
          },
        }}
      />
    </>
  );
};

export default BarChart;
