import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";


import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import {
  faCog,
  faSearch,
  faCommentAlt,
  faBell,
  faAngleDown,
} from "@fortawesome/free-solid-svg-icons";

import "../Stylings/topNavBar.css";
import developer from "../Images/developer.png";

const TopBar = ({ endpoint }) => {
   useState("");

  useEffect(() => {
    // setDropDown("none");
  }, []);

  const drop = () => {
    console.log(document.getElementById("menu").style.display);

    if (document.getElementById("menu").style.display !== "flex") {
      document.getElementById("menu").style.display = "flex";
    } else if (document.getElementById("menu").style.display !== "none") {
      document.getElementById("menu").style.display = "none";
    }
  };

  return (
    <>
      <div id="topBar">
        <div id="searchBar">
          {/* <p>Search bar</p> */}
          {/* <i class="fa fa-search icon"></i> */}
          <FontAwesomeIcon
            
            icon={faSearch}
            className="icon"
          />
          <input
            id="searchInput"
            type="text"
            placeholder="Type to search..."
            autoComplete="off"
          />
        </div>
        <div id="options">
          <Link to="/settings">
            <div
              style={{
                padding: "5px",
                borderRadius: "5px",
                backgroundColor:
                  endpoint === "/settings" ? "#6100ec" : "transparent",
              }}
            >
              <FontAwesomeIcon
                className="adjust"
                icon={faCog}
                style={{
                  color: "#898585",
                  opacity: endpoint === "/settings" ? 1 : 0.4,
                }}
              />
            </div>
          </Link>

          <Link to="/inbox">
            <div
              style={{
                padding: "5px",
                borderRadius: "5px",
                backgroundColor:
                  endpoint === "/inbox" ? "#6100ec" : "transparent",
              }}
            >
              <FontAwesomeIcon
                className="adjust"
                icon={faCommentAlt}
                style={{
                  color: "#898585",
                  opacity: endpoint === "/inbox" ? 1 : 0.4,
                }}
              />
            </div>
          </Link>

          <Link to="/support">
            <div
              style={{
                padding: "5px",
                borderRadius: "5px",
                backgroundColor:
                  endpoint === "/support" ? "#6100ec" : "transparent",
              }}
            >
              <FontAwesomeIcon
                className="adjust"
                icon={faBell}
                style={{
                  color: "#898585",
                  opacity: endpoint === "/support" ? 1 : 0.4,
                }}
              />
            </div>
          </Link>
        </div>
        <div onClick={drop} >
          <div id="dropDown" >
            <div id="name">
              <p>Thomas Brown</p>
              <p id="dev">Developer</p>
            </div>
            <div id="imgHere">
              <img src={developer} alt="Me"></img>
              <FontAwesomeIcon
                className="adjust"
                id="drop"
                icon={faAngleDown}
              />
            </div>
          </div>
          <div id="menu">
            <Link to="/dashboard">
              <p className="dropDownItem">Dashboard</p>
            </Link>
            <Link to="/discover">
              <p className="dropDownItem">Discover</p>
            </Link>
            <Link to="/users">
              <p className="dropDownItem">Users</p>
            </Link>
            <Link to="/documents">
              <p className="dropDownItem">Documents</p>
            </Link>
            <Link to="/settings">
              <p className="dropDownItem">Settings</p>
            </Link>
            <Link to="/logOut">
              <p className="dropDownItem">LogOut</p>
            </Link>
          </div>
        </div>
      </div>
    </>
  );
};

export default TopBar;
