import React, { useEffect, useState } from "react";


import {
  
  Card,
  CardBody,
  CardHeader,
  CardFooter,
  CardTitle,
  
  ButtonGroup,
  
} from "reactstrap";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowAltCircleUp } from "@fortawesome/free-solid-svg-icons";
import Base from "./base";
import BarChart from "./barChart";
import LineChart from "./lineChart";
import AGgrid from "./agGrid";

import "../Stylings/dashboard.css";

const Dashboard = () => {
  const [income, setIncome] = useState("");
  const [yearlyProgress, setYearlyProgress] = useState();
  const [users, setUsers] = useState();
  const [earnings, setEarnings] = useState();
  const [sales, setSales] = useState();
  const [barLabels, setBarLabels] = useState();
  const [barData, setBarData] = useState();
  const [lineMonthsLabels, setLineMonthsLabels] = useState([]);
  const [lineMonthsData, setLineMonthsData] = useState([]);
  const [lineYearsLabels, setLineYearsLabels] = useState([]);
  const [lineYearsData, setLineYearsData] = useState([]);
  const [increament, setIncreament] = useState({
    incomeInc: 6.9,
    usersInc: 6.9,
    earningInc: 2.5,
    salesInc: 13.6,
  });

  const { incomeInc, usersInc, earningInc, salesInc } = increament;

  const [month, setMonth] = useState(true);
  const [year, setYear] = useState(false);

  const [rowData1, setRowData1] = useState([]);
  const [columnDefs1, setColumnDefs1] = useState([]);
  const [rowData2, setRowData2] = useState([]);
  const [columnDefs2, setColumnDefs2] = useState([]);

  useEffect(() => {
    document.getElementById("monthsChart").style.display = "inline-block";
    document.getElementById("yearsChart").style.display = "none";

    document.getElementById("invoicesBody").style.display = "none";
    document.getElementById("incomingBody").style.display = "inline-block";

    setIncome("$124,563.00");
    setYearlyProgress(70);

 

    setUsers(94.2);
    setEarnings(43.41);
    setSales("$95,422");

    setBarLabels(["", "", "", "", "", "", "", ""]);
    setBarData([6, 3, 15, 7, 10, 12, 7, 3]);
    

    setLineMonthsLabels(["", "", "", "", "", "", "", "","","","","","","","","","","","","","","","","","",""]);
    setLineMonthsData([6, 4.6, 5.5, 4, 7, 12, 3, 11,21,16,14,17.5,15,13,19,10,10,10,23,20,21,19,15,17,12,16]);
    

    setColumnDefs1([
      {
        headerName: "Invoice Id",
        field: "IID",
        filter: true,
        sortable: true,
        checkboxSelection: true,
      },
      { headerName: "Date", field: "date", filter: true, sortable: true },
      {
        headerName: "Description",
        field: "desc",
        filter: true,
        sortable: true,
      }, { headerName: "Status", field: "status", filter: true, sortable: true },
      {
        headerName: "Descriptions",
        field: "descs",
        filter: true,
        sortable: true,
      },
     
    ]);

    setRowData1([
      { 
        IID: "M06359DR0",
        date: "08.04.2021",
        desc: "Code 5928MD01",
        status: "Completed",
        descs: "$2500.00",
        
      },
      {
        IID: "M06359DR0",
        date: "05.04.2021",
        desc: "Code 5928OP11",
        descs: "$2410.00",
        status: "Completed",
      },
      {
        IID: "M06359DR0",
        date: "04.04.2021",
        desc: "Code 6928MD01",
        descs: "$250.00",
        status: "Completed",
      },
      {
        IID: "M06359DR0",
        date: "08.03.2021",
        desc: "Code 8168MD01",
        descs: "$6358.00",
        status: "Completed",
      },
      {
        IID: "M06359DR0",
        date: "14.04.2021",
        desc: "Code 7228MD01",
        descs: "$5263.00",
        status: "Completed",
       
      },
    ]);

    setColumnDefs2([
        {
            headerName: "Invoice Id",
            field: "IID",
            filter: true,
            sortable: true,
            checkboxSelection: true,
          },
          { headerName: "Date", field: "date", filter: true, sortable: true },
          {
            headerName: "Description",
            field: "desc",
            filter: true,
            sortable: true,
          }, { headerName: "Status", field: "status", filter: true, sortable: true },
          {
            headerName: "Descriptions",
            field: "descs",
            filter: true,
            sortable: true,
          },
    ]);

    setRowData2([
        { 
            IID: "M06359DR0",
            date: "08.04.2021",
            desc: "Code 5928MD01",
            status: "Completed",
            descs: "$2500.00",
          },
          {
            IID: "M06359DR0",
            date: "05.04.2021",
            desc: "Code 5928OP11",
            descs: "$2410.00",
            status: "Completed",
          },
          {
            IID: "M06359DR0",
            date: "04.04.2021",
            desc: "Code 6928MD01",
            descs: "$250.00",
            status: "Completed",
          },
          {
            IID: "M06359DR0",
            date: "08.03.2021",
            desc: "Code 8168MD01",
            descs: "$6358.00",
            status: "Completed",
          },
          {
            IID: "M06359DR0",
            date: "14.04.2021",
            desc: "Code 7228MD01",
            descs: "$5263.00",
            status: "Completed",
          },
    ]);
  }, []);

  const handelMonthly = (e) => {
    e.preventDefault();
    e.stopPropagation();

    document.getElementById("monthsChart").style.display = "inline-block";
    document.getElementById("yearsChart").style.display = "none";

    document.getElementById("btnYear").classList.remove("activebtn");
    document.getElementById("btnMonth").classList.add("activebtn");

    
  };

  const handelYearly = (e) => {
    e.preventDefault();
    e.stopPropagation();

    document.getElementById("monthsChart").style.display = "none";
    document.getElementById("yearsChart").style.display = "inline-block";

    document.getElementById("btnMonth").classList.remove("activebtn");
    


  };

  const handelIncoming = (e) => {
    e.preventDefault();
    e.stopPropagation();

    document.getElementById("invoicesBody").style.display = "none";
    document.getElementById("incomingBody").style.display = "inline-block";

    document.getElementById("invoices").classList.remove("activespan");
    document.getElementById("incoming").classList.add("activespan");
  };

  const handelInvoices = (e) => {
    e.preventDefault();
    e.stopPropagation();

    document.getElementById("incomingBody").style.display = "none";
    document.getElementById("invoicesBody").style.display = "inline-block";

    document.getElementById("incoming").classList.remove("activespan");
    document.getElementById("invoices").classList.add("activespan");
  };

  return (
    <Base endpoint="/dashboard">
      <div id="dashboard">
        <div id="charts">
          <div id="leftCharts">
            <div id="leftUpperChart">
              <Card className="card">
                <CardHeader className="cardHeader">
                  <h5>Total Income</h5>
                  <CardTitle className="cardTitle">
                    <span>{income}</span>
                    <span className="increment">+{incomeInc}%</span>
                  </CardTitle>
                </CardHeader>
                <CardBody className="cardBody">
                  {/* <Progress value="{yearlyProgress}" color="danger"/> */}
                  <div id="progressBar">
                    <div id="seven0"></div>
                    <div id="three0"></div>
                  </div>
                </CardBody>
                <CardFooter id="cardFooter">Yearly Goal</CardFooter>
              </Card>
            </div>
            <div id="leftLowerChart">
              <Card className="card">
                <CardHeader className="cardHeader">
                  <h5>New Users</h5>
                  <CardTitle className="cardTitle">
                    <span>{users}%</span>
                    <span className="increment">+{usersInc}%</span>
                  </CardTitle>
                </CardHeader>
                <CardBody className="cardBody">
                  <BarChart label={barLabels} data={barData} />
                </CardBody>
              </Card>
            </div>
          </div>
          <div id="rightCharts">
            <Card className="card">
              <CardHeader className="cardHeader">
                <div className="headAdjust">
                  <h5>Balance</h5>
                  {/* <p>Button</p> */}
                  <ButtonGroup className="btnGrp">
                    <button
                      type="button"
                      className="btn activebtn"
                      id="btnMonth"
                      
                    >
                      Monthly
                    </button>
                   
                  </ButtonGroup>
                </div>

                <CardTitle id="cardTitleSpecial">
                  <div id="earnings">
                    <Card className="card">
                      <CardHeader className="cardHeader">
                        <h5>Earnings</h5>
                        <CardTitle className="cardTitle">
                          <span>{earnings}%</span>
                          <span className="increment">+{earningInc}%</span>
                        </CardTitle>
                      </CardHeader>
                    </Card>
                  </div>
                  <div id="salesValue">
                    <Card className="card">
                      <CardHeader className="cardHeader">
                        <h5>Sales Value</h5>
                        <CardTitle className="cardTitle">
                          <span>{sales}%</span>
                          <span className="increment">+{salesInc}%</span>
                        </CardTitle>
                      </CardHeader>
                    </Card>
                  </div>
                </CardTitle>
              </CardHeader>
              <CardBody className="cardBody">
                <div>
                  <div id="monthsChart">
                    <LineChart
                      label={lineMonthsLabels}
                      data={lineMonthsData}
                      title="Label-2"
                    />
                  </div>
                  <div id="yearsChart">
                    <LineChart
                      label={lineYearsLabels}
                      data={lineYearsData}
                      title="Label-2"
                    />
                  </div>

                  {/* {month === true && (
                    <LineChart label={lineMonthsLabels} data={lineMonthsData} />
                  )}
                  {year === true && (
                    <LineChart label={lineYearsLabels} data={lineYearsData} />
                  )} */}
                </div>
              </CardBody>
            </Card>
          </div>
        </div>
        <div id="agGrids">
          {/* <AGgrid /> */}
          <Card className="card">
            <CardHeader className="cardHeader">
              <div className="headAdjust">
                <div id="upperAG">
                  <h5>Recent Transactions</h5>
                  <button className="btn activebtn">
                    <FontAwesomeIcon icon={faArrowAltCircleUp} />
                    <p>Export</p>
                  </button>
                </div>
                <div id="lowerAG">
                  <span
                    id="incoming"
                    className="activespan"
                    onClick={handelIncoming}
                  >
                    Incoming
                  </span>
                  <span id="invoices" onClick={handelInvoices}>
                    Invoices
                  </span>
                </div>
              </div>
              <CardTitle className="cardTitle"></CardTitle>
            </CardHeader>
            <CardBody className="cardBody">
              <div id="incomingBody">
                <AGgrid columnDefs={columnDefs1} rowData={rowData1} />
              </div>
              <div id="invoicesBody">
                <AGgrid columnDefs={columnDefs2} rowData={rowData2} />
              </div>
            </CardBody>
          </Card>
        </div>
      </div>
    </Base>
  );
};

export default Dashboard;

